import { useState, useEffect } from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";
import { Formik } from "formik";
import axios from "axios";
import MobileDatePicker from "@material-ui/lab/MobileDatePicker";

import {
  Box,
  Card,
  TextField,
  Typography,
  MenuItem,
  Button,
  FormControl,
  Select,
  InputLabel,
  FormHelperText,
} from "@material-ui/core";
import userService from "../apis/userService";
import { Section } from "../HelperFunction";
const Department = [
  {
    value: "0",
    label: "nur",
  },
  {
    value: "1",
    label: "Prep",
  },
  {
    value: "2",
    label: "one",
  },
];
const ProjectDetailsForm = (props) => {
  const { onBack, onNext, allStudentAttendence, setSelectedClass, ...other } = props;

  // console.log("student", student);
  const [classRoom, setClassRoom] = useState([]);
  const [section, setSection] = useState([]);

  // const classData = async () => {
  //   const classData = await userService.getApi("class/api");
  //   setClassRoom(classData.data);
  // };

  // const section_api = async () => {
  //   const sectionData = await userService.getApi("class/api/section"
  //   );
  //   // console.log(sectionData.data)
  //   setSection(sectionData.data);
  // };
  useEffect(() => {
    const fun = Section("class/api");
    fun.then((res) => {
      setClassRoom(res);
    });
    const fun1 = Section("class/api/section");
    fun1.then((res) => {
      setSection(res);
    });
  }, []);

  return (
    <Formik
      initialValues={{
        classid: "",
        sectionid: "",
        date: null,
      }}
      validationSchema={Yup.object().shape({
        // classid: Yup.array().of(
        // Yup.object().shape({
        // classid: Yup.string().min(8, "Min 8 chars").max(28, "Max 28 chars"),
        // Rest of your amenities object properties
        // })
        // ),
        date: Yup.date()
          .min(new Date("2016/12/31").toLocaleDateString())
          .max(new Date().toLocaleDateString())
          .required(),
      })}
      onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
        try {
          setSelectedClass({
            ...values,
          });
          // alert(JSON.stringify(values), null);
          // Call API to store step data in server session

          const data = { ...values };

          
          if (onNext) {
            allStudentAttendence(data);
            onNext();
          }
          setStatus({ success: true });
          setSubmitting(false);
        } catch (err) {
          console.error(err);
          setStatus({ success: false });
          setErrors({ submit: err.message });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        setFieldValue,
        setFieldTouched,
        touched,
        values,
      }) => (
        <form onSubmit={handleSubmit} {...other}>
          <Card sx={{ p: 3 }}>
            <Typography color="textPrimary" variant="h6">
              Select Attendence Info And Detail
            </Typography>
            <Box sx={{ mt: 2 }}>
              <FormControl style={{ margin: 8, width: "50%" }}>
                <InputLabel id="classid">Department/Class</InputLabel>

                <Select
                  fullWidth
                  labelId="classid"
                  value={values.classid}
                  onChange={handleChange}
                  id="classid"
                  label="Department/Class"
                  name="classid"
                >
                  {classRoom.map((option) => (
                    <MenuItem key={option.id} value={option.id}>
                      {option.className}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              <FormControl style={{ margin: 8, width: "50%" }}>
                <InputLabel id="sectionid">Section</InputLabel>

                <Select
                  fullWidth
                  labelId="sectionid"
                  value={values.sectionid}
                  onChange={handleChange}
                  id="sectionid"
                  label="Section"
                  name="sectionid"
                >
                  {section.map((option) => (
                    <MenuItem key={option.id} value={option.id}>
                      {option.name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              <Box
                sx={{
                  display: "flex",
                  mt: 4,
                }}
              >
                <Box sx={{ ml: 1.2 }}>
                  <MobileDatePicker
                    fullwidth
                    label="Date"
                    onAccept={() => setFieldTouched("date")}
                    onChange={(date) =>
                      setFieldValue("date", date.toLocaleDateString())
                    }
                    inputFormat="yyyy/MM/dd"
                    onClose={() => setFieldTouched("date")}
                    renderInput={(inputProps) => (
                      <TextField variant="outlined" {...inputProps} />
                    )}
                    value={values.date}
                  />
                </Box>
              </Box>
              {Boolean(touched.date && errors.date) && (
                <Box sx={{ mt: 2 }}>
                  <FormHelperText error>{errors.date}</FormHelperText>
                </Box>
              )}
            </Box>
            <Box
              sx={{
                display: "flex",
                mt: 6,
              }}
            >
              {onBack && (
                <Button
                  color="primary"
                  onClick={onBack}
                  size="large"
                  variant="text"
                >
                  Previous
                </Button>
              )}
              <Box sx={{ flexGrow: 1 }} />
              <Button
                color="primary"
                disabled={isSubmitting}
                type="submit"
                variant="contained"
              >
                Next
              </Button>
            </Box>
          </Card>
        </form>
      )}
    </Formik>
  );
};

ProjectDetailsForm.propTypes = {
  onBack: PropTypes.func,
  onNext: PropTypes.func,
};

export default ProjectDetailsForm;
