import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import {
  Divider,
  FormControl,
  FormControlLabel,
  FormLabel,
  Radio,
  RadioGroup,
  Typography,
} from "@material-ui/core";

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

function createData(rollno, name, section) {
  return { rollno, name, section };
}

const rows = [
  createData(159, "Frozen yoghurt", "Section A", 6.0),
  createData(159, "Ice cream sandwich", "Section A", 9.0),
  createData(159, "Eclair", "Section A", 16.0),
  createData(159, "Cupcake", "Section A", 3.7),
  createData(159, "Gingerbread", "Section A", 16.0),
];

export default function AttendenceTable( props ) {
  const {student,...other}=props
  const classes = useStyles();

  // if(!student) return null
  return (
    <>
      <Typography color="textPrimary" variant="h6">
        Select Attendence Info And Detail
      </Typography>
      <Divider />
      <TableContainer>
        <Table className={classes.table} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>Roll No</TableCell>
              <TableCell colSpan={1}>Student Name</TableCell>
              <TableCell colSpan={1}>Class</TableCell>
              <TableCell colSpan={1}>Section</TableCell>

              <TableCell>Attendence</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {student.length>0?student.map((row) => (
              <TableRow key={row.id}>
                <TableCell component="th">{row.rollno}</TableCell>
                <TableCell>{row.name}</TableCell>
                <TableCell>{row.class}</TableCell>

                <TableCell>{row.section}</TableCell>
                <TableCell>
                  <FormControl>
                    <RadioGroup
                      row
                      aria-labelledby="demo-radio-buttons-group-label"
                      defaultValue="present"
                      name="radio-buttons-group"
                    >
                      <FormControlLabel
                        value="present"
                        control={<Radio />}
                        label="Present"
                      />
                      <FormControlLabel
                        value="absent"
                        control={<Radio />}
                        label="Absent"
                      />
                      <FormControlLabel
                        value="late"
                        control={<Radio />}
                        label="Late"
                      />{" "}
                      <FormControlLabel
                        value="latewithexcuse"
                        control={<Radio />}
                        label="Late With Excuse"
                      />
                    </RadioGroup>
                  </FormControl>
                </TableCell>
              </TableRow>
            )): "No Data Found"}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
}
