import { useState } from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";
import {
  Box,
  Button,
  Card,
  CircularProgress,
  FormHelperText,
  Typography,
} from "@material-ui/core";
import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
  Divider,
  FormControl,
  FormControlLabel,
  FormLabel,
  Radio,
  RadioGroup,
} from "@material-ui/core";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import { Field, Formik } from "formik";
import Student_list from "../Student_list";
import AttendenceTable from "./AttendenceTable";
import wait from "../../utils/wait";
const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

const AttendenceList = (props) => {
  const classes = useStyles();
  const { onBack, onComplete, student, attendanceRecord, ...other } = props;
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(null);
  return (
    <Formik
      initialValues={{
        picked: [],
        submit: null,
      }}
      validationSchema={Yup.object().shape({})}
      onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
        try {
          if (onComplete) {
            setLoading(true);
            await wait(2000);
            onComplete();
          }
        } catch (err) {
          console.error(err);
          setError(err.message);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        setFieldValue,
        setFieldTouched,
        touched,
        values,
      }) => (
        <form onSubmit={handleSubmit} {...other}>
          <Card sx={{ p: 3 }}>
            {/* <AttendenceTable student={student} /> */}
            <Typography color="textPrimary" variant="h6">
              Select Attendence Info And Detail
            </Typography>
            <Divider />
            <TableContainer>
              <Table className={classes.table} aria-label="simple table">
                <TableHead>
                  <TableRow>
                    <TableCell>Roll No</TableCell>
                    <TableCell colSpan={1}>Student Name</TableCell>
                    <TableCell colSpan={1}>Class</TableCell>
                    <TableCell colSpan={1}>Section</TableCell>

                    <TableCell>Attendence</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {student.length > 0 ? (
                    student.map((row) => (
                      <TableRow key={row.id}>
                        <TableCell component="th">{row.rollno}</TableCell>
                        <TableCell
                        // id=""
                        //  value={values.id}
                        >
                          {`${row.name} ${row.lname}`}
                        </TableCell>
                        <TableCell>{row.class}</TableCell>

                        <TableCell>{row.section}</TableCell>
                        <TableCell>
                          {/* <label>
                              <Field type="radio" name="picked" value="One" />
                              One
                            </label>
                            <label>
                              <Field type="radio" name="picked" value="Two" />
                              Two
                            </label> */}
                          <FormControl>
                            <RadioGroup
                              row
                              aria-labelledby="demo-radio-buttons-group-label"
                              defaultValue="1"
                              name="radio-buttons-group"
                            >
                              <FormControlLabel
                                value="present"
                                // onChange={(e)=>{handleChanges(e,row.id)}}
                                onChange={() => {
                                  attendanceRecord(row, "1");
                                }}
                                name="present"
                                // value="Two"
                                control={<Radio />}
                                label="Present"
                              />
                              <FormControlLabel
                                name="absent"
                                value="absent"
                                // onChange={(e)=>{handleChanges(e,row.id)}}
                                onChange={() => {
                                  attendanceRecord(row, "2");
                                  // setSelectedStudents({
                                  //   ...row,
                                  //   absentPresent: "absent",
                                  // });
                                }}
                                // value="Two"
                                control={<Radio />}
                                label="Absent"
                              />
                              <FormControlLabel
                                value="3"
                                name="late"
                                onChange={() => {
                                  attendanceRecord(row, "3");
                                }}
                                control={<Radio />}
                                label="Late"
                              />{" "}
                              <FormControlLabel
                                value="leave"
                                onChange={() => {
                                  attendanceRecord(row, "4");
                                }}
                                name="leave"
                                control={<Radio />}
                                label="Leave"
                              />
                            </RadioGroup>
                          </FormControl>
                        </TableCell>
                      </TableRow>
                    ))
                  ) : (
                    <TableRow
                      style={{
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                      }}
                    >
                      Data Not Found{" "}
                    </TableRow>
                  )}
                </TableBody>
              </Table>
            </TableContainer>
            {error && (
              <Box sx={{ mt: 2 }}>
                <FormHelperText error>{FormHelperText}</FormHelperText>
              </Box>
            )}
            <Box
              sx={{
                display: "flex",
                mt: 6,
              }}
            >
              {onBack && (
                <Button
                  color="primary"
                  onClick={onBack}
                  size="large"
                  variant="text"
                >
                  Previous
                </Button>
              )}
              <Box sx={{ flexGrow: 1 }} />
              <Button
                startIcon={
                  loading && isSubmitting ? (
                    <CircularProgress size="1rem" />
                  ) : null
                }
                color="primary"
                disabled={isSubmitting}
                type="submit"
                variant="contained"
              >
                {loading && isSubmitting ? "Save Attendence" : "Save"}
              </Button>
            </Box>
          </Card>
        </form>
      )}
    </Formik>
  );
};

AttendenceList.propTypes = {
  onBack: PropTypes.func,
  onComplete: PropTypes.func,
};

export default AttendenceList;
