import React, { useState } from "react";
import { Box, Container } from "@material-ui/core";
import { useEffect } from "react";
import EnhancedTable from "./Pagination";
import axios from "axios";
import userService from "./apis/userService";
import { useNavigate } from "react-router-dom";
import { customAxios } from "./apis/Axios";
import { Helmet } from "react-helmet-async";
import Header_Design from "./Transport/Header_Design"
import useSettings from "../hooks/useSettings";
const headCells = [
  { id: "rollno", numeric: true, disablePadding: false, label: "Roll No" },

  {
    id: "name",
    numeric: false,
    disablePadding: false,
    label: "Name",
  },
  { id: "fname", numeric: true, disablePadding: false, label: "Parents " },
  { id: "gender", numeric: true, disablePadding: false, label: "Gender" },
  { id: "class", numeric: true, disablePadding: false, label: "Class" },
  { id: "section", numeric: true, disablePadding: false, label: "Section" },
  // { id: "address", numeric: true, disablePadding: false, label: "Address" },
  // { id: "dob", numeric: true, disablePadding: false, label: "dob" },
  { id: "phone", numeric: true, disablePadding: false, label: "Phone" },
  { id: "email", numeric: true, disablePadding: false, label: "E-mail" },
  // { id: "", numeric: false, disablePadding: false, label: "" },
];

const Student_list = () => {
  const settings=useSettings()
  const Navigate = useNavigate();
  const [rows, setrows] = useState([]);
  const [refresh, setRefresh] = useState(true);
 const [first, setfirst] = useState(new Date().getFullYear());
  const Addmission_date = async () => {
    try {
      const data = await customAxios.getCalls("list/list",{
      params: { yearid: first },
      });
      setrows(data);
      console.log(data);
    } catch (e) {
      console.log("tikees", e);
    }
  };

  useEffect(() => {
    Addmission_date();
  }, [refresh, first]);


useEffect(()=>{
  console.log("first", first);
})

  return (
    <>
      <Helmet>
        <title>Dashboard: Invoice List | Material Kit Pro</title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: "background.default",
          minHeight: "100%",
          py: 8,
        }}
      >
        <Container maxWidth={settings.compact ? "xl" : false}>
          <Header_Design text="All Student Data" dashText="Transport Detail" />
          <Box sx={{ mt: 3 }}>
            <EnhancedTable
              setfirst={setfirst}
              invoices={rows}
              url="list/del"
              refresh={refresh}
              setRefresh={setRefresh}
              navUrl="Student-admission-form"
            />
          </Box>
        </Container>
      </Box>
    </>
  );
};
export default Student_list;
