import { useState } from "react";
import { Link as RouterLink } from "react-router-dom";
import {
  Avatar,
  Box,
  Button,
  Card,
  CardContent,
  Typography,
} from "@material-ui/core";
import StarIcon from "@material-ui/icons/Star";
// import ProjectDescriptionForm from "./ProjectDescriptionForm";
import AttendenceList from "./AttendenceList";
import AttendenceForm from "./AttendenceDateForm";
import axios from "axios";
import { useEffect } from "react";
import toast from "react-hot-toast";
import userService from "../apis/userService";
import {customAxios} from "../apis/Axios";
const AttendenceWizad = (props) => {
  // console.log(props)
  const [activeStep, setActiveStep] = useState(0);
  const [completed, setCompleted] = useState(false);
  const [student, setStudent] = useState([]);
  const [loading, setLoding] = useState(true);
  const [selectedStudents, setSelectedStudents] = useState([]);
  const [selectedClass, setSelectedClass] = useState({});
  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };
const extraData = Object.assign({}, student);
// const extraData=student.reduce((a, v) => ({ ...a, [v]: v }), {});
// console.log("extraData", extraData);
  const handleComplete = async () => {
    // setCompleted(true);
    const selectedClassCopy = { ...selectedClass };
    const selectedStudentsCopy = [...selectedStudents];
const months = [
  "Jan",
  "Feb",
  "Mar",
  "Apr",
  "May",
  "June",
  "July",
  "Aug",
  "Sep",
  "Oct",
  "Nov",
  "Dec",
];
const d = new Date(selectedClassCopy.date);
var name = months[d.getMonth()];

var timeStr = selectedClassCopy.date;
var date = new Date(timeStr);
var day = date.getDate();
var year = date.getFullYear();
var month = name;
var dateStr = day + "/" + month + "/" + year;
    const combine = {
      ...selectedClassCopy,
      attendence_list: [...selectedStudentsCopy],
    };
// console.log("combine",combine)
    await customAxios.postCall("attendence/attendence/post", {
        ...combine,
      })
      .then((res) => {
        if (res.status === 403) {
          toast.success(`Attendence Update On ${( dateStr)}`);
          setCompleted(true);

        } else {
          toast.success(`Attendence Taken On Date ${dateStr} `);
          setCompleted(true);
        }
      });

    // console.log(combine);
  };

  const allStudentAttendence = async (data) => {
    if(data==undefined){
      return toast.error("plz filled the feild")
    }
    const studentData = await customAxios.getCalls("attendence/api/attendence",
      {
        params: {
          ...data,
        },
      }
    );
    // console.log(studentData.data);
    setStudent(studentData);
    //  res.json;
  };



  // useEffect(() => {
  //   console.log(selectedStudents);

  // }, [selectedStudents]);

  const attendanceRecord = (student, absentpresent) => {
    const selectedStudentCopy = [...selectedStudents];
    let obj = {
      studentname: student.name,
      studentid: student.id,
      rollno: student.rollno,
      absentPresent: absentpresent,
    };
    const findIndex = selectedStudentCopy.findIndex((res) => {
      // console.log("res",res)
      return res.studentid == student.id;
    });

    if (findIndex >= 0) {
      selectedStudentCopy[findIndex] = obj;
    } else {
      selectedStudentCopy.push({ ...obj });
    }

    setSelectedStudents(selectedStudentCopy);
  };

  return (
    <div {...props}>
      {!completed ? (
        <>
          {activeStep === 0 && (
            <AttendenceForm
              onNext={handleNext}
              allStudentAttendence={allStudentAttendence}
              setSelectedClass={setSelectedClass}
            />
          )}
          {/* {activeStep === 1 && (
            <AttendenceList onBack={handleBack} onNext={handleNext} />
          )} */}
          {activeStep === 1 && (
            <AttendenceList
              attendanceRecord={attendanceRecord}
              student={student}
              onBack={handleBack}
              onComplete={handleComplete}
            />
          )}
        </>
      ) : (
        <Card>
          <CardContent>
            <Box
              sx={{
                maxWidth: 450,
                mx: "auto",
              }}
            >
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                }}
              >
                <Avatar
                  sx={{
                    backgroundColor: "primary.main",
                    color: "primary.contrastText",
                  }}
                >
                  <StarIcon fontSize="small" />
                </Avatar>
              </Box>
              <Box sx={{ mt: 2 }}>
                <Typography align="center" color="textPrimary" variant="h3">
                  Attendence Save
                </Typography>
              </Box>
              <Box sx={{ mt: 2 }}>
                <Typography
                  align="center"
                  color="textSecondary"
                  variant="subtitle1"
                >
                  {`Of Class ${extraData[0].class} ${extraData[0].section} On Date ${selectedClass.date}`}
                </Typography>
              </Box>
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  mt: 2,
                }}
              >
                <Button
                  color="primary"
                  component={RouterLink}
                  to="/dashboard/attendencelist"
                  variant="contained"
                >
                  View Attendence
                </Button>
              </Box>
            </Box>
          </CardContent>
        </Card>
      )}
    </div>
  );
};

export default AttendenceWizad;
